import React, { useState, useRef, useEffect } from 'react';
import { createPortal } from 'react-dom';
import './FloatingText.css';
import { calculateTileCenter } from '../VFX/VFX.js';
import { useStrings } from '../UI/StringsContext';

const FloatingTextManager = () => {
    const [tick, setTick] = useState(0);
    const floatingTextsRef = useRef([]);
    const addFloatingTextRef = useRef(null);
    const strings = useStrings();

    const forceUpdate = () => setTick((prev) => prev + 1);

    const instanceId = Math.floor(Math.random() * 10000);
console.log(` FloatingTextManager mounted. Instance ID: ${instanceId}`);

    // Assign stable function to the static handler
    useEffect(() => {
        FloatingTextManager.addFloatingText = (message, x, y, TILE_SIZE) => {
            const displayText = typeof message === 'number'
                ? (strings[message] || `Missing string for code: ${message}`)
                : message;

            console.log(' Adding floating text:', { message, displayText, x, y });

            const container = document.querySelector('.homestead');
            if (!container) return;

            if (isNaN(x) || isNaN(y)) {
                console.warn('Invalid coordinates in addFloatingText:', { x, y });
                return;
            }

            const { centerX, centerY } = calculateTileCenter(x, y, TILE_SIZE);
            const newId = `${Date.now()}-${Math.floor(Math.random() * 10000)}`;

            floatingTextsRef.current.push({
                id: newId,
                text: displayText,
                x: centerX,
                y: centerY,
                timestamp: Date.now()
            });

            forceUpdate();
        };
    }, [strings]);

    const container = document.querySelector('.homestead');
    if (!container) return null;

    return createPortal(
        <div className="floating-text-container">
            {floatingTextsRef.current.map(({ id, text, x, y }) => (
                <div
                    key={id}
                    className="floating-text"
                    style={{
                        position: 'absolute',
                        left: x,
                        top: y - 30,
                        transform: 'translate(-50%, -50%)'
                    }}
                    onAnimationEnd={() => {
                        floatingTextsRef.current = floatingTextsRef.current.filter(t => t.id !== id);
                        forceUpdate();
                    }}
                >
                    {text}
                </div>
            ))}
        </div>,
        container
    );
};

// Will be assigned inside useEffect
FloatingTextManager.addFloatingText = (...args) => {
    console.warn('FloatingTextManager not ready yet', ...args);
};

export default FloatingTextManager;